.hamburger-button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media (min-width: 1150px) {
  .hamburger-button {
    display: none;
  }
}

.hamburger-button::before {
  pointer-events: none;
  content: '';
  position: fixed;
  top: 35px;
  right: 30px;
  z-index: 20;
  width: 50px;
  height: 50px;
  direction: rtl;
  background-color: #15181d;
  border-radius: 100%;
  animation-name: outNavbarBackground;
  animation-duration: 0.4s;
  animation-direction: reverse;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
}

.hamburger-button--close {
  padding: 0;
  margin: 0;
}

.hamburger-button--close::before {
  content: '';
  position: fixed;
  top: 25px;
  right: 25px;
  z-index: 20;
  width: 50px;
  height: 50px;
  direction: rtl;
  background-color: #15181d;
  border-radius: 100%;
  animation-name: inNavbarBackground;
  animation-duration: 0.4s;
  animation-direction: normal;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

.hamburger-button--close .hamburger-button__icon {
  animation-name: inMain;
  animation-duration: 0.8s;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.hamburger-button--close .hamburger-button__icon::before {
  animation-name: inBefore;
  animation-duration: 0.8s;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.hamburger-button--close .hamburger-button__icon::after {
  animation-name: inAfter;
  animation-duration: 0.8s;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.hamburger-button__icon {
  position: relative;
  z-index: 30;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #bf890e;
  animation-name: outMain;
  animation-duration: 0.8s;
  animation-direction: reverse;
  animation-fill-mode: backwards;
}

.hamburger-button__icon::before, .hamburger-button__icon::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #bf890e;
  transform-origin: 50% 50%;
  animation-duration: 0.8s;
  animation-direction: reverse;
  animation-fill-mode: backwards;
}

.hamburger-button__icon::before {
  transform: translateY(-10px);
  animation-name: outBefore;
}

.hamburger-button__icon::after {
  transform: translateY(10px);
  animation-name: outAfter;
}

